'use client';

import { Container, Title, Text, Button, Group, SimpleGrid, Card, Image } from '@mantine/core';
import { IconCalendar, IconBuildingCommunity } from '@tabler/icons-react'; // Using tabler icons as lucide might need mapping or direct import

export default function Home() {
    return (
        <main>
            {/* Hero Section */}
            <div style={{
                height: '80vh',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                textAlign: 'center',
                background: 'linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(0,0,0,0.05) 100%)'
            }}>
                <Title order={1} size="4rem" style={{ fontWeight: 800, letterSpacing: '-0.02em', marginBottom: '1rem' }}>
                    Nonghyup Broadcasting
                </Title>
                <Text size="xl" c="dimmed" style={{ maxWidth: 600, marginBottom: '2rem' }}>
                    Connecting people through premium broadcasting facilities and seamless scheduling.
                </Text>
                <Group>
                    <Button size="lg" variant="gradient" gradient={{ from: 'blue', to: 'cyan' }}>
                        Book a Facility
                    </Button>
                    <Button size="lg" variant="default">
                        View Schedule
                    </Button>
                </Group>
            </div>

            {/* Features Section */}
            <Container size="lg" py="xl">
                <SimpleGrid cols={{ base: 1, sm: 2 }} spacing="xl">
                    <Card padding="xl" radius="lg" withBorder>
                        <Card.Section>
                            <div style={{ height: 200, background: '#f1f3f5', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                <IconBuildingCommunity size={64} color="#adb5bd" />
                            </div>
                        </Card.Section>
                        <Title order={3} mt="md">State-of-the-art Facilities</Title>
                        <Text c="dimmed" mt="sm">
                            Experience our world-class auditorium and conference rooms equipped with the latest technology.
                        </Text>
                    </Card>
                    <Card padding="xl" radius="lg" withBorder>
                        <Card.Section>
                            <div style={{ height: 200, background: '#f1f3f5', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                <IconCalendar size={64} color="#adb5bd" />
                            </div>
                        </Card.Section>
                        <Title order={3} mt="md">Easy Scheduling</Title>
                        <Text c="dimmed" mt="sm">
                            Check availability and book your events seamlessly with our integrated scheduling system.
                        </Text>
                    </Card>
                </SimpleGrid>
            </Container>
        </main>
    );
}
